@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style-type: none;
}

body {
  background-image: url('https://cdn.pixabay.com/photo/2016/10/21/14/50/plouzane-1758197_960_720.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  position: relative;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
  z-index: -1;
}
.timer {
  display: flex;
}
.value {
  font-size: 4rem;
}

.title span {
  display: block;
  font-size: 5rem;
  letter-spacing: 2rem;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 50vh;
}

.start {
  width: 350px;
  font-size: 1.5rem;
  text-align: center;
  border: 2px solid #fff;
  padding: 0px 15px;
  border-radius: 12px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  background: none;
  box-sizing: border-box;
  color: #fff;
  min-width: 100px;
  padding: 10px;
  margin-top: 40px;
}

.start:hover {
  background-color: #fff;
  color: #003269;
  cursor: pointer;
}

.date-value {
  text-align: center;
  border: 2px solid #fff;
  padding: 0px 20px;
  border-radius: 12px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  // background-color: #858585;
  background: none;
  box-sizing: border-box;
  color: #fff;
  min-width: 150px;
  font-size: 2rem;
  margin-top: 40px;
}

.timer > li {
  margin: 0 2rem;
  text-align: center;
  border: 2px solid #fff;
  padding: 0px 20px;
  border-radius: 12px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  color: #fff;
  min-width: 150px;
}
@media (max-width: 992px) {
  .timer > li {
    margin: 0 1rem;
  }
}
@media (max-width: 767px) {
  .timer {
    flex-wrap: wrap;
  }
  .timer > li {
    margin: 1rem auto;
    max-width: 150px;
  }
  .title {
    font-size: 1.5rem;
    line-height: 3.5rem;
    margin: 10px 0px;
  }
  .title span {
    font-size: 2rem;
    letter-spacing: 1rem;
  }
}

.title {
  display: flex;
  font-family: 'Open Sans', sans-serif;
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}


.back{
  font-size: 25px;
  margin-top: 20px;
  margin-right: auto;
}


.back:hover{
  color: #fff;
  text-decoration: none;
}